import './../src/styles/style.scss';
import './../src/styles/vars.scss';
import './../src/styles/header_menu.scss';
import Header from "./components/Header";
import MenuMain from './components/Menu_main';
import Footer from './components/Footer';


import createSetCards from './js/getSetCards';
import dataMenu from './json/menu.json';
import {showMenu, hideMenu} from './js/changeBurger';

import favi from'./assets/icons/coffee-cup.png';
import cardOne from './assets/images/coffee-1.jpg';
import cardTwo from './assets/images/coffee-2.jpg';
import cardThree from './assets/images/coffee-3.jpg';
import cardFour from './assets/images/coffee-4.jpg';
import cardFive from './assets/images/coffee-5.jpg';
import cardSix from './assets/images/coffee-6.jpg';
import cardSeven from './assets/images/coffee-7.jpg';
import cardEight from './assets/images/coffee-8.jpg';
import teaOne from './assets/images/tea-1.png';
import teaTwo from './assets/images/tea-2.png';
import teaThree from './assets/images/tea-3.png';
import teaFour from './assets/images/tea-4.png';
import dessertOne from './assets/images/dessert-1.png';
import dessertTwo from './assets/images/dessert-2.png';
import dessertThree from './assets/images/dessert-3.png';
import dessertFour from './assets/images/dessert-4.png';
import dessertFive from './assets/images/dessert-5.png';
import dessertSix from './assets/images/dessert-6.png';
import dessertSeven from './assets/images/dessert-7.png';
import dessertEight from './assets/images/dessert-8.png';
                
const container = document.querySelector('.container');


Header(container);
MenuMain(container);
Footer(container);       

const burger = document.querySelector('.menu-burger');
const spans = burger.querySelectorAll('span');
const menu = document.querySelector('.menu');
const buttonsBlock = document.querySelector('.buttons');
const buttons = buttonsBlock.querySelectorAll('button');
const boxCards = document.querySelector('.menu_main-cards');
const buttonMore = document.querySelector('.menu_main-refresh');

let set = 'coffee';
createSetCards(boxCards,dataMenu,set);



burger.addEventListener('click',(event)=>{
    event.target.classList.contains('burger-active')||
    event.target.classList.contains('span-active')?
        hideMenu(burger,spans, menu):
        showMenu(burger,spans, menu);
} );

menu.addEventListener('click', (event) => {
    if(event.target.classList.contains('menu-link')||
       event.target.classList.contains('menu-list-link')){
        event.preventDefault();
        hideMenu(burger,spans, menu);
        setTimeout(()=>{
            window.location =event.target.href
        }, 500);
}
});

buttonsBlock.addEventListener('click', (event) => {

    if(event.target.classList.contains('it')){
        set = event.target.classList[0];
        buttons.forEach(button =>{
            button.classList.contains(set)?
            button.classList.add('active'):
            button.classList.remove('active');
        })
    }    

    boxCards.innerHTML='';
    createSetCards(boxCards,dataMenu,set);
    boxCards.children.length == 4?
    buttonMore.classList.add('host'):
    buttonMore.classList.remove('host');
});


 
buttonMore.addEventListener('click', ()=>{
    const cards = document.querySelectorAll('.card');
    cards.forEach(card=>{
    card.style = 'display:flex';
    })
    buttonMore.classList.add('host');
});








export default container;

