import './../styles/vars.scss';
import './../styles/card.scss';
import Card from '../components/Card';


const createSetCards = (parent ,menu, category) =>{    
    menu.forEach((item, index)=> {
        if(item.category == category){
        Card(parent,
             item,
             index);
        }
         
    });

   
}

export default createSetCards;